:root {
  --font-family-Amiko: "Amiko";
  --font-family-Book-antiqua: "Book-antiqua";
  --font-family-Carattere: "Carattere";
  --font-family-MavenPro: "MavenPro";
  --content-width-min: 71.146vw;
  --container-offset: 0.781vw;
  --container-width: calc(
    var(--content-width-min) + (var(--container-offset) * 2)
  );
  --light-color: #fff;
  --black-color: #363636;
  --red-color: #db3f5a;
  --gray-color: #1e1e1e;
  --blue-color: #d7e8ef;
}

