.titles {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-bottom: 2vw;
}

.title {
  font-family: var(--font-family-Book-antiqua), sans-serif;
  font-weight: 400;
  font-size: 2.917vw;
  line-height: 3.49vw;
  text-transform: uppercase;
  padding-right: 0.729vw;
}

.label {
  font-family: var(--font-family-Carattere), sans-serif;
  font-weight: 400;
  font-size: 5.521vw;
  line-height: 6.302vw;
  text-transform: lowercase;
  color: var(--red-color);
}

.header {
  padding: 0.7vw 5.729vw;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: var(--light-color);
  -webkit-box-shadow: 0px 3px 8px 2px rgba(34, 60, 80, 0.2);
  -moz-box-shadow: 0px 3px 8px 2px rgba(34, 60, 80, 0.2);
  box-shadow: 0px 3px 8px 2px rgba(34, 60, 80, 0.2);
  z-index: 10;
}

.nav__list {
  display: flex;
  align-items: center;
}
.nav__item {
  display: flex;
  margin-right: 2.344vw;
}
.nav__item:last-child {
  margin-right: 1vw;
}
.nav__item-logo {
  margin: 0 auto;
  width: 6.875vw;
  height: 4.375vw;
}
.nav__item-phone {
  display: flex;
  align-items: center;
}
.nav__item-phone svg {
  width: 1.3vw;
  height: 1.3vw;
  fill: #000;
  padding-right: 0.417vw;
  transition: all 0.2s ease-in-out;
}
.nav__link {
  font-size: 1.042vw;
  line-height: 1.224vw;
  font-weight: 600;
  text-transform: uppercase;
  color: var(--black-color);
  transition: all 0.2s ease-in-out;
}
.nav__link-phone {
  display: flex;
  align-items: center;
}

.nav__item:hover svg,
.nav__link:hover {
  color: var(--red-color);
  fill: var(--red-color);
}

.hero {
  padding-top: 5.8vw;
  height: 40.292vw;
  margin-bottom: 7.292vw;
}
.hero__inner {
  display: flex;
  height: 100%;
  position: relative;
  overflow: hidden;
}
.hero__inner::after {
  content: "";
  position: absolute;
  top: -8vw;
  left: 46.5%;
  width: 4vw;
  height: 150%;
  background-color: var(--red-color);
  transform: rotate(40.8deg);
  z-index: 3;
}
.hero__content {
  text-align: center;
  padding: 2.469vw 6.5vw 6vw;
  position: absolute;
  top: 24vw;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--light-color);
  box-shadow: 0vw 0.208vw 2.604vw rgba(0, 0, 0, 0.15);
  border-radius: 1.042vw;
  z-index: 3;
}
.hero__text {
  margin-bottom: 0;
}
.hero__title {
  position: relative;
  padding-bottom: 1.042vw;
  margin-bottom: 1.042vw;
}
.hero__title::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 6.458vw;
  height: 0.052vw;
  background-color: var(--black-color);
}
.hero__subtitle span {
  text-transform: lowercase;
}
.hero__label {
  position: absolute;
  top: 4vw;
  left: 50%;
  transform: translate(-50%, -50%);
}
.hero__image {
  height: 100%;
}
.hero__image img {
  height: 100%;
  animation: bg 10s ease-in-out infinite;
  animation-direction: alternate;
}
.hero__image--meet {
  width: 60%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  clip-path: polygon(0 0, 100% 0, 50% 100%, 0% 100%);
  z-index: 2;
}
.hero__image--fish {
  position: absolute;
  top: 0;
  right: 0;
}

@keyframes bg {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.3);
  }
}
.our-products {
  margin-bottom: 8.292vw;
}
.our-products__container {
  position: relative;
}
.our-products__bubbles {
