  top: 1.6vw;
  left: 0;
  z-index: -1;
}
.our-products__inner {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 1.302vw;
  grid-row-gap: 1.302vw;
}
.our-products__item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  padding: 2.604vw 2.188vw;
  height: 15.625vw;
  background-color: var(--light-color);
  box-shadow: 0vw 0.208vw 2.083vw rgba(0, 0, 0, 0.1);
